*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.demoBox{
    margin-top: 0.1px;
    padding: 20px;
}
#demo{
    word-wrap:break-word;
    overflow:auto;
    line-height: 1.8;
}
.box{
    background-color: #f8f8f8;
}
.Diagrams{
    position: absolute;
    right: 20px;
    top: 20px;
    width: 300px;
    height: 300px;
    
}

.Diagrams::after,.Diagrams::before{
    content: '';
    display: block;
    width:150px;
    height: 150px;
    box-sizing: border-box;
    position: absolute;
    left:50%;
    border-radius:50%;
}
@media(max-width:500px){
    .demoBox{
        width: 100vw;
        height: 50vh;
        box-sizing: border-box;
        font-size: 18px;
    }
    .demoBox #demo{
        height: calc(50vh - 40px);
    }
    .box{
        height: 50vh;
        position: relative;
    }
    .Diagrams{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
}
@media(min-width:501px){
    body{
        display: flex;
    }
    .demoBox{
        width: 50vw;
        height: 100vh;
        padding: 20px;
    }
    .demoBox #demo{
        width: calc(50vw - 40px);
        height: calc(100vh - 40px);
        box-sizing: border-box;
        
    }
    .box{
        width: 50vw;
        height: 100vh;
        position: relative;
    }
    .Diagrams{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
}